import React, { Component, Suspense } from 'react';
import { Switch, Route } from 'react-router-dom';
import Loadable from 'react-loadable';

import '../../node_modules/font-awesome/scss/font-awesome.scss';
import 'antd/dist/antd.css';
import Loader from './layout/Loader'
import Aux from "../hoc/_Aux";
import ScrollToTop from './layout/ScrollToTop';
import routes from "../route";
import LoginForm from '../Demo/Authentication/SignIn/SignIn1';
const AdminLayout = Loadable({
    loader: () => import('./layout/AdminLayout'),
    loading: Loader
});

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
          logged_in: localStorage.getItem('token') ? true : false,
          username: ''
        };
    }
    
      componentDidMount() {
        if (this.state.logged_in) {
          fetch('http://34.93.135.63/api/current_user/', {
            headers: {
              Authorization: `JWT ${localStorage.getItem('token')}`
            }
          })
            .then(res => res.json())
            .then(json => {
              this.setState({ 
                logged_in: true,username: json.username });
            })
        }
      }
    
      handle_login = (e, data) => {
        e.preventDefault();
        fetch('http://34.93.135.63/token-auth/', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(data)
        })
            .then(res => res.json())
            .then(json => {
                const expirationDate = new Date(new Date().getTime() + 3600 * 10000);
                localStorage.setItem('token', json.token);
                localStorage.setItem('expirationDate', expirationDate);
                this.setState({
                logged_in: true,
                username: json.user.username
                });
            })
      };
    
      handle_logout = () => {
        localStorage.removeItem('token');
        this.setState({ logged_in: false, username: '' });
      };
    

    render() {
        const menu = routes.map((route, index) => {
          return (route.component) ? (
              <Route
                  key={index}
                  path={route.path}
                  exact={route.exact}
                  name={route.name}
                  render={props => (
                      <route.component {...props} />
                  )} />
          ) : (null);
        });

        return (
            <div className="App">
              {this.state.logged_in && this.state.username
                ? 
                <Aux>      
                  <ScrollToTop>
                      <Suspense fallback={<Loader/>}>
                          <Switch>
                              {menu}
                              <Route path="/" component={AdminLayout} logged_in={this.state.logged_in} username={this.state.username}/>
                          </Switch>
                      </Suspense>
                  </ScrollToTop>
                </Aux>
                : <LoginForm handle_login={this.handle_login} />}
          </div>
        );
    }
}

export default App;
